import Vue from 'vue';
import PixelHabbo from '../../../PixelHabbo';
import FollowUserIdComposer from '../../../networking/composers/pixelhabbotool/FollowUserIdComposer';
import Html from './html/Chatlog.html';

export default Vue.extend({

    template: Html,

    data: function () {
        return {
            data: PixelHabbo.GetStore(),
        }
    },
    computed: {
        GetLogs: function () {
            return PixelHabbo.GetStore().chatlog_pub.slice(0).reverse();
        }
    },
    methods: {
        isOdd: function (Num: number) {
            return Num % 2;
        },
        FollowUser: function (UserId: number) {
            PixelHabbo.GetWebSocketManager().SendPacket(new FollowUserIdComposer(UserId));
        }
    }
});