import IPacketEvent from '../IPacketEvent';
import ClientPacket from '../ClientPacket';
import PixelHabbo from '../../../PixelHabbo';

export default class RpTrocAccepteEvent implements IPacketEvent {

    public Parse(packet: ClientPacket) {

        let UserId: number = packet.PopInt();
        let Etat: boolean = packet.PopBoolean();

        if(UserId == PixelHabbo.GetStore().rp_troc_target_settings.userid)
            PixelHabbo.GetStore().rp_troc_target_settings.accepte = Etat;
        else
            PixelHabbo.GetStore().rp_troc_settings.accepte = Etat;
    }
}