import Vue from 'vue';
import PixelHabbo from '../../../PixelHabbo';
import Html from './html/Tool.html';

export default Vue.extend({

    template: Html,

    data: function () {
        return {
            data: PixelHabbo.GetStore()
        }
    },

    methods: {
        ToggleChatlogPub: function () {
            PixelHabbo.GetStore().pixelhabbotool_chatlogpub = !PixelHabbo.GetStore().pixelhabbotool_chatlogpub;
        },
        ToggleHotel: function () {
            PixelHabbo.GetStore().pixelhabbotool_hotelalert = !PixelHabbo.GetStore().pixelhabbotool_hotelalert;
        }
    }
});