import IPacketEvent from '../IPacketEvent';
import ClientPacket from '../ClientPacket';
import PixelHabbo from '../../../PixelHabbo';
import SoundType from '../../../sound/SoundType';

export default class NotifAlertEvent implements IPacketEvent {

    public Parse(packet: ClientPacket) {
        PixelHabbo.GetStore().notif_alert_open = true;
        PixelHabbo.GetStore().notif_alert_image = packet.PopString();
        PixelHabbo.GetStore().notif_alert_title = packet.PopString();
        PixelHabbo.GetStore().notif_alert_message = packet.PopString();
        PixelHabbo.GetStore().notif_alert_textbutton = packet.PopString();
        PixelHabbo.GetStore().notif_alert_roomid = packet.PopInt();
        PixelHabbo.GetStore().notif_alert_url = packet.PopString();

        if(PixelHabbo.GetStore().notif_alert_url == "")
            PixelHabbo.GetTimeoutManager().CloseNotifAlert();

        if(PixelHabbo.GetStore().notif_alert_roomid > 0)
            PixelHabbo.GetSoundManager().playSound("animation_warn", SoundType.SYSTEME, false);
    }

}