import Vue from 'vue';
import PixelHabbo from '../../../PixelHabbo';
import JoinRoomIdComposer from '../../../networking/composers/notif/JoinRoomIdComposer';
import Html from './html/NotifAlert.html';

export default Vue.extend({
    
    template: Html,

    data: function () {
        return {
            data: PixelHabbo.GetStore()
        }
    },

    methods: {
        Close: function () {
            PixelHabbo.GetTimeoutManager().CloseNotifAlert(true);
        },
        JoinRoom: function () {
            if(PixelHabbo.GetStore().notif_alert_url == "")
            {
                PixelHabbo.GetTimeoutManager().CloseNotifAlert(true);
                PixelHabbo.GetWebSocketManager().SendPacket(new JoinRoomIdComposer(PixelHabbo.GetStore().notif_alert_roomid));
            } else {
                if(window == null)
                    return;
                let openurl = window.open(PixelHabbo.GetStore().notif_alert_url, "_blank");
                if(openurl == null)
                    return;
                openurl.focus();
            }
        }
    }

});