import IPacketEvent from '../IPacketEvent';
import ClientPacket from '../ClientPacket';
import PixelHabbo from '../../../PixelHabbo';

export default class RpTrocStopEvent implements IPacketEvent {

    public Parse(packet: ClientPacket) {

        PixelHabbo.GetStore().rp_inventory_troc_open = false;
        PixelHabbo.GetStore().rp_troc_target_settings = { userid: 0, username: "", accepte: false, confirme: false };
        PixelHabbo.GetStore().rp_troc_settings = { accepte: false, confirme: false };
        
        PixelHabbo.GetStore().rp_troc_my_items.splice(0, PixelHabbo.GetStore().rp_troc_my_items.length); //Clean
        PixelHabbo.GetStore().rp_troc_target_items.splice(0, PixelHabbo.GetStore().rp_troc_target_items.length); //Clean
    }
}