import Vue from 'vue';
import PixelHabbo from '../../../PixelHabbo';
import RpBotChooseComposer from '../../../networking/composers/roleplay/RpBotChooseComposer';
import Html from './html/RpBotChoose.html';

export default Vue.extend({
    template: Html,

    data: function () {
        return {
            data: PixelHabbo.GetStore(),
        }
    },
    computed: {
        GetChoose: function () {
            return PixelHabbo.GetStore().rp_botchoose;
        },
    },
    methods: {
        Click: function (Code: string) {
            if(Code != "")
                PixelHabbo.GetWebSocketManager().SendPacket(new RpBotChooseComposer(Code));
                
            PixelHabbo.GetStore().rp_botchoose.splice(0, PixelHabbo.GetStore().rp_botchoose.length); //Clean
        }
    }
});