import IPacketEvent from '../IPacketEvent';
import ClientPacket from '../ClientPacket';
import PixelHabbo from '../../../PixelHabbo';

export default class InRoomEvent implements IPacketEvent {

    public Parse(packet: ClientPacket) {
        let InRoom: boolean = packet.PopBoolean();
        PixelHabbo.GetStore().in_room = InRoom;
        PixelHabbo.GetStore().rpbox_buyitems_open = false;
        PixelHabbo.GetStore().rp_botchoose.splice(0, PixelHabbo.GetStore().rp_botchoose.length); //Clean

        PixelHabbo.GetSoundManager().stopSound();
    }

}