import IPacketEvent from '../IPacketEvent';
import ClientPacket from '../ClientPacket';
import PixelHabbo from '../../../PixelHabbo';

export default class RpStatsEvent implements IPacketEvent {

    public Parse(packet: ClientPacket) {
        let RpId: number = packet.PopInt();

        if(RpId != PixelHabbo.GetStore().rp_id)
        {
            PixelHabbo.GetStore().rp_inventory.splice(0, PixelHabbo.GetStore().rp_inventory.length);
            PixelHabbo.GetStore().rp_item_choice.count = 0;
        }

        PixelHabbo.GetStore().rp_id = RpId;
        PixelHabbo.GetStore().rp_mode = (RpId > 0);
        PixelHabbo.GetStore().rp_health = packet.PopInt();
        PixelHabbo.GetStore().rp_maxhealh = packet.PopInt();
        PixelHabbo.GetStore().rp_energy = packet.PopInt();
        PixelHabbo.GetStore().rp_money = packet.PopInt();
        PixelHabbo.GetStore().rp_munition = packet.PopInt();
        PixelHabbo.GetStore().rp_level = packet.PopInt();
    }

}