import IPacketEvent from '../IPacketEvent';
import ClientPacket from '../ClientPacket';
import PixelHabbo from '../../../PixelHabbo';

export default class NotifTopEvent implements IPacketEvent {

    public Parse(packet: ClientPacket) {
        PixelHabbo.GetStore().notif_top_open = true;
        PixelHabbo.GetStore().notif_top_message = packet.PopString();

        PixelHabbo.GetTimeoutManager().InitSendNotifTop();
        PixelHabbo.GetTimeoutManager().CloseNotifTop();
    }

}