import IPacketEvent from '../IPacketEvent';
import ClientPacket from '../ClientPacket';
import PixelHabbo from '../../../PixelHabbo';

export default class NotifTopInitEvent implements IPacketEvent {

    public Parse(packet: ClientPacket) {
        let Count = packet.PopInt();

        if(Count > 0)
        {
            for(let i: number = 0;i < Count; i++)
            {
                PixelHabbo.GetStore().notif_top_list_message.push(packet.PopString());
            }
        }

        PixelHabbo.GetTimeoutManager().SendNotifTop();
        PixelHabbo.GetTimeoutManager().InitSendNotifTop();
    }
    
}